.footer {
  text-align: center;
  padding: 20px;
  background: #333;
  color: #ddd;
}

.social-links a {
  color: #aaa;
  margin: 0 10px;
  font-size: 1.5rem;
  transition: color 0.3s ease, transform 0.3s ease;
}

.social-links a:hover {
  color: #00aaff;
  transform: scale(1.2);
}
